@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap);
body {
}



@font-face{font-family:horror;src:url("/Hangman/static/media/shlop rg.d4944d69.ttf")}*{margin:0;padding:0;-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0;-webkit-margin-start:0;margin-inline-start:0;-webkit-margin-end:0;margin-inline-end:0;box-sizing:border-box;font-family:horror;color:white;background-color:black;text-align:center;-webkit-user-select:none;-ms-user-select:none;user-select:none}.playBtn{color:#484545;font-size:clamp(2rem, calc(2rem + 2vw), 4rem);-webkit-user-select:auto;-ms-user-select:auto;user-select:auto}.playBtn:hover{text-shadow:5px 5px 5px #dfd3d3}.playBtn:active{color:#fcf3f3}.game_container{margin:1rem 0;display:flex;align-items:center;justify-content:space-evenly}

header{color:#e0e0e0;display:flex;flex-direction:column;justify-content:center;align-items:center;margin:0.5rem 0}header h1{font-size:clamp(4rem, calc(3rem + 3vw), 6rem);color:#e0e0e0;transition:all 1.5s ease}header .watermark{position:relative;left:20%}header .watermark span{color:#4b4b4b;font-size:2rem}header .watermark #HXN{font-family:'Amatic SC', cursive;color:#e0e0e0;font-size:3rem;transition:all 1.5s ease}

.figure-container{fill:transparent;stroke:#fff;stroke-width:4px;stroke-linecap:round}.figure-part{transition:stroke 1s ease}

.guessWord{margin:1rem 0;display:flex;justify-content:center;align-items:center}.guessWord span{color:blueviolet;font-size:calc(1.8rem + 1vw);width:1rem;height:3.5rem;margin:0.25rem;border-bottom:0.2rem solid #8a2be2}.guessWord .red-hightlight{-webkit-animation:fade ease-in-out 1.5s;animation:fade ease-in-out 1.5s;color:red;border-bottom-color:red}.guessWord .green-highlight{-webkit-animation:fade ease-in-out 1.5s;animation:fade ease-in-out 1.5s;color:#38ff00;border-bottom-color:#38ff00}.notification{margin-bottom:1rem;text-align:center;min-height:calc(1.2rem + 1vw);color:#670000;font-size:calc(1.2rem + 1vw)}.hint{display:flex;flex-direction:column;justify-content:center;align-items:center;font-size:clamp(1rem, calc(0.5rem + 2vw), 2rem);-webkit-animation:fade ease-in-out 1.5s;animation:fade ease-in-out 1.5s}.hint p{letter-spacing:0.001rem}@-webkit-keyframes fade{0%{opacity:0}100%{opacity:1}}@keyframes fade{0%{opacity:0}100%{opacity:1}}

.mistake h2{color:#670000;font-size:clamp(2rem, calc(1rem + 5vw), 4.5rem)}.mistake .usedLetter{color:#969090;font-size:calc(1.3rem + 1vw)}

